import DashboardHeader from '../features/dashboard/DashboardHeader';
import Sidebar from '../features/dashboard/Sidebar';
import CurrentLesson from '../features/dashboard/CurrentLesson';
import LearningTools from '../features/dashboard/LearningTools';
import StatsPanel from '../features/dashboard/StatsPanel';
import ChatButton from '../features/dashboard/ChatButton';

export default function Home() {
  return (
    <div className="min-h-screen bg-white flex">
      {/* Sidebar */}
      <Sidebar />

      {/* Main Content */}
      <div className="flex-1 flex flex-col">
        {/* Header */}
        <DashboardHeader />

        {/* Content Area */}
        <div className="flex-1 flex gap-6 p-6 overflow-auto">
          {/* Left Column - Main Content */}
          <div className="flex-1 max-w-4xl">
            <CurrentLesson />
            <LearningTools />
          </div>

          {/* Right Column - Stats */}
          <div className="flex-shrink-0">
            <StatsPanel />
          </div>
        </div>
      </div>

      {/* Floating Chat Button */}
      <ChatButton />
    </div>
  );
}
